<template lang="pug">
.cate-page
    .back(v-if="data")
        button(@click="$ga.event('籤詩內容','點擊回選單',getCateID,getPoemID); $router.push({name: 'cate', params: {'cate_id': getCateID}})") 《 回選單
    .data(v-if="data" v-html="data") 

    .loading(v-if="loading") Loading ...

    .error(v-if="error") {{ error }}
    br
    br
    hr
    br
    br
    img(v-if="data" :src=" '/static/api/' + getCateID + '/' + getPoemID + '.jpg'") 
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            loading: false,
            data: null,
            error: null
        }
    },
    created () {
        this.fetchData()
    },
    computed: {
        getPoemID() {
            return this.$route.params.poem_id
        },
        getCateID() {
            return this.$route.params.cate_id
        }
    },
    methods: {
        fetchData () {
            this.error = this.data = null
            this.loading = true
            axios.get('/static/api/' + this.$route.params.cate_id + '/' + this.$route.params.poem_id + '.html').then((res)=>{
                this.loading = false
                this.data = res.data
            }).catch(error => {
                this.loading = false
                this.error = error.toString()
            })
        }
    }
}
</script>

<style>

</style>



