<template lang="pug">
.cate-page
    div(v-if="$route.params.cate_id == '關於我們'")
        h2 關於我們
        p 『籤詩小舖』提供籤詩資料瀏覽，包含最常見的雷雨師一百籤(台北行天宮)、六十甲子籤(主要見於媽祖廟、天公廟)、觀音一百籤...等等。<br><br>為提供更好的瀏覽體驗，網站與其中的籤詩資料將持續新增，歡迎各界多加引用。
        p <br>聯絡方式：<a href="mailto:fortunesticks888@gmail.com">fortunesticks888@gmail.com</a><br><br><br>
        h2 資料來源
        p <br>感謝<a href="http://www.chance.org.tw/" target="__blank">籤詩網</a>開放籤詩資料。<br><br>
    div(v-else-if="$route.params.cate_id == null")
        h2 籤詩介紹
        p 籤文是指信徒求籤後得解惑用的文句。由於常以詩歌形式表達，又稱籤詩。東亞傳統宗教的廟宇常會設置給信眾問卜用籤，求籤後得相應的籤文，就是由神佛給予的「答案」。<br><br>現行寺廟盛行的籤詩，依不同籤詩數量，通常可分為<router-link to="雷雨師一百籤">雷雨師一百籤</router-link>、<router-link to="六十甲子籤">六十甲子籤</router-link>、觀音一百籤、保生大帝六十籤、觀音廿八籤、觀音24籤、澎湖天后宮一百籤、金錢掛三二籤、註生娘娘三十籤、呂祖六十籤、廿八宿籤。 — <a href="https://zh.wikipedia.org/wiki/%E7%B1%A4%E6%96%87" target="_blank">wiki</a>
    div(v-else-if="$route.params.cate_id")
        .loading(v-if="loading") Loading ...
        .error(v-if="error") {{ error }}
        div(v-if="data && data.poems != null")
            h2 籤詩選單
            button(v-for="poem in data.poems" @click="$ga.event('籤詩選單 - ' + poem.cate_id,'點擊項目',poem.poem_id); $router.push({name: 'poem', params: {'cate_id': poem.cate_id, 'poem_id': poem.poem_id}});") {{ poem.title }}
        br
        br
        .data(v-if="data" v-html="data.content") 
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            loading: false,
            data: null,
            error: null
        }
    },
    created () {
        this.fetchData()
    },
    computed: {
    },
    methods: {
        sendEvent(...params) {
            this.$ga.event(...params)
        },
        fetchData () {
            this.error = this.data = null
            this.loading = true
            if (this.$route.params.cate_id) {
                axios.get('/static/api/' + this.$route.params.cate_id + '.json').then((res)=>{
                    this.loading = false
                    this.data = res.data
                }).catch(error => {
                    this.loading = false
                    this.error = error.toString()
                })
            }
        }
    }
}
</script>

<style>

</style>



